* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
  padding: 0;
}
li{
  display: flex;
}
img{
  margin: 10px;
  width: auto;
  height: auto;
  max-width: 60px;
  max-width: 56px;
  display: block;
}
.info-box{
  max-height: 50px;
  display: flex;
  column-gap: 5px;
align-items: center;
}
p{ font-size: 20px;}

